import React, { Component } from 'react'

export default class Notfound extends Component {
    render() {
        return (
            <>
            <div className="jumbotron bg-danger text-white">
                <h1 className="display-4">Page Not Found!</h1>
                <span className="lead">404</span>
            </div>
            </>
        )
    }
}
