import React, { Component } from 'react'
import axios from "axios"
export default class Movies extends Component {

    state={Movies:[],Tv:[]}
    getTranding= async (mediaType)=>{
        let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/day?api_key=52bbcddeda849047525b51d6f8a12361`)
        // console.log(data.results);
        this.setState({
            [mediaType]:data.results
        })
    }
    componentDidMount(){
        this.getTranding("Movies")
        this.getTranding("Tv")
    }
    render() {
        return (
            <>
            <div className="container my-5">
                <div className="row">
                    <div className="col-md-4">
                        <div className="item">
                            <div className="brdr w-25"></div>
                            <h1>Tranding <br /> Movies <br /> To Watch now</h1>
                            <p className='secColor'>Most watched movies by data</p>
                            <div className="brdr w-100"></div>
                        </div>
                    </div>
                    {this.state.Movies.slice(10).map((value,index)=>{
                        return(
                            <div key={index} className="col-md-2 my-5">
                                <div className="item">
                                    <img className='w-100' src={"https://image.tmdb.org/t/p/original"+value.poster_path} alt="" />
                                    <h5>{value.name} {value.title}</h5>
                                    <p>{value.overview.slice(0,50)}..</p>
                                    <span className='Vote'>{value.vote_average}</span>
                                </div>
                            </div>
                        )
                    })}
                </div>

                <div className="row">
                    <div className="col-md-4">
                        <div className="item">
                            <div className="brdr w-25"></div>
                            <h1>Tranding <br /> Movies <br /> To Watch now</h1>
                            <p className='secColor'>Most watched movies by data</p>
                            <div className="brdr w-100"></div>
                        </div>
                    </div>
                    {this.state.Tv.slice(10).map((value,index)=>{
                        return(
                            <div key={index} className="col-md-2 my-5">
                                <div className="item">
                                    <img className='w-100' src={"https://image.tmdb.org/t/p/original"+value.poster_path} alt="" />
                                    <h5>{value.name} {value.title}</h5>
                                    <p>{value.overview.slice(0,50)}..</p>
                                    <span className='Vote'>{value.vote_average}</span>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
            </>
        )
    }
}