import React, { Component } from 'react'
import axios from 'axios'
export default class Register extends Component {
    user={
        first_name:"",
        last_name:"",
        email:"",
        password:""
    }
    getDataForm = (e)=>{
        this.user[e.target.name]=e.target.value
    }
    sendData = async (e) =>{
        e.preventDefault();
        let {data} = await axios.post("https://route-egypt-api.herokuapp.com/signup",this.user)
        if (data.message === "success") {
            window.location.replace(`/login`);
            e.target.reset()
        }
    }
    render() {
        return (
            <>
            <div className="w-75 mx-auto my-5">
                <form onSubmit={this.sendData} action="">
                    <input onChange={this.getDataForm} name="first_name" type="text" placeholder="First Name" className="form-control mt-3" />
                    <input onChange={this.getDataForm} name="last_name"  type="text" placeholder="Last Name" className="form-control mt-3" />
                    <input onChange={this.getDataForm} name="email" type="email" placeholder="Email" className="form-control mt-3" />
                    <input onChange={this.getDataForm} name="password" type="password" placeholder="password" className="form-control mt-3" />
                    <button type="submit" className="btn btn-info float-left mt-3">Register</button>
                </form>
            </div>
            </>
        )
    }
}